import { get, writable } from "svelte/store";
import { image, label } from "~/stores/render-options";
const defaultXY = (index, fontSize, imageWidth) => {
    const labelSpan = fontSize * 2;
    const maxLabelXPos = imageWidth - fontSize;
    let x = index * labelSpan;
    let y = Math.floor(x / maxLabelXPos) * labelSpan;
    if (maxLabelXPos < x) {
        x = x % maxLabelXPos;
        x = x - (x % labelSpan);
    }
    return { x, y };
};
export default {
    ...writable([]),
    add(text) {
        const { x, y } = defaultXY(get(this).length, get(label).fontSize, get(image).width);
        this.update((list) => [...list, { text, x, y }]);
    },
    remove(index) {
        this.update((list) => {
            list.splice(index, 1);
            return [...list];
        });
    }
};
